@import "../../../Styles/properties.scss";
.container {
  background: var(#{--body-background-default});
  .app-router {
    margin: 20px;
  }
  .startPage {
    display: grid;
    grid-template: 350px 1fr/1fr;
    background: var(#{--body-background-default});

    .weather-container {
      display: grid;
      justify-content: space-between;
      grid-template: 350px / 300px 750px;
      align-content: center;
      line-height: 0px;

      .location-info {
        background: var(#{--components-background-default});
        box-shadow: 1px 1px 32px 2px rgba(34, 60, 80, 0.2);
        border: none;
        border-radius: 20px; // justify-content: center;
        padding: 5px;

        .info {
          display: flex;
          justify-content: center;
          align-items: center;
          .temperature {
            font-size: 80px;
            font-weight: bold;
            color: #4793ff;
          }
        }
        .text {
          display: flex;
          flex-direction: column;
          align-items: center;
        }
        p {
          font-size: 25px;
          font-weight: 300;
          color: #939cb0;
        }
        img {
          width: 125px;
        }
      }
    }
    .days-div {
      margin-top: 50px;
      background-color: var(#{--body-background-default});
      .days {
        background-color: var(#{--components-background-default});

        margin-top: 10px;
        display: grid;
        grid-template: 300px / 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        box-shadow: $box-shadow;
        border-radius: $border-radius;
        padding: 20px;
        gap: 10px;
        line-height: 20px;
        .day {
          background-color: var(#{--card-background-default});
          border-radius: $border-radius;
          padding: 20px;
          .day-temp {
            // color: var(#{--text-color-default});
            font-weight: bold;
            font-size: 1.5rem;
          }
          .night-temp {
            font-weight: 300;
            font-size: 1.2rem;
          }
          .day-descript {
            font-weight: 300;
            font-size: 1.2rem;
          }
        }
      }
      .header-days {
        display: grid;
        grid-template: 1fr/144px 144px 144px;
        gap: 15px;
      }
    }
  }
  .startPage {
    @media (max-width: 1600px) {
      margin: 50px;
    }
  }
}
